<template>
  <div class="hs-container">
    <c-header class="c-header" :title="`${$t('base.HotelBooking')}-${Number(interHotelOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}`" isBackNav></c-header>
    <hs-scroll
      v-if="!noValue(JSON.stringify(hotel))"
      class="table-view">
      <ul>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="custom-cell-hotel">
              <p class="title">{{hotel.NameChn}}({{hotel.NameEng}})</p>
              <p class="little-title">{{ratePlan.RoomNameChn}}</p>
              <p>{{ratePlan.BedType}} | {{$t('base.CanAccommodateXPeople', 'function', interHotelOrder.AdultAmount)}} | {{ratePlan.BreadFast}}</p>
              <p>{{$t('base.CheckInDate')}} {{$t(interHotelOrder.CheckInDate, 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}</p>
              <p>{{$t('base.CheckOutDate')}} {{$t(interHotelOrder.CheckOutDate, 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}} {{$t('base.TotalNights', 'function', getApartDayCount(interHotelOrder.CheckInDate, interHotelOrder.CheckOutDate))}}</p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false" v-if="!noValue(ratePlan.CancelRule)">
            <div class="custom-cell-cancel">
              <div style="background-image: url('static/image/hotel/irreversible.png')"></div>
              <div>
                <p>{{ratePlan.CancelRule.CancelRuleDesc}}</p>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>

        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false">
            <div class="custom-cell-room">
              <div @click="showRoomAmount = !showRoomAmount">
                <div :type="language">
                  <p>{{$t('base.NumberOfRooms')}}</p>
                  <p>{{interHotelOrder.RoomAmount}}{{$t('base.QuantifierHotel')}}<span>{{$t('base.CanAccommodateUpToXPeople', 'function', interHotelOrder.AdultAmount)}}</span></p>
                </div>
                <div class="arrow" :style="{transform: `rotate(${showRoomAmount ? '90deg' : '-90deg'})`}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
              </div>
              <div v-if="showRoomAmount">
                <p v-for="n in 5" :key="n" :class="{active: interHotelOrder.RoomAmount === n}" @click="_setRoomAmount(n)">{{n}}</p>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>

        <!-- 入住人信息 -->
        <hs-section v-for="index in interHotelOrder.RoomAmount" :key="index">
          <hs-cell-1 :showLogo="false">
            <div class="custom-cell-guest">
              <div class="header">
                <div :type="language">
                  <p>{{$t('base.Room')}}</p>
                  <p>{{$t('base.FillInAccordingToTheActualNumber')}}</p>
                </div>
                <hs-button type="common" fontSize="0.8rem" @click.native="_delegateWithAdd(index - 1)">{{$t('base.Add')}} +</hs-button>
              </div>
              <div class="cell" v-for="n in interHotelOrder.AdultAmount" :key="`AdultAmount${n}`">
                <div class="view" :style="{transform: `translateX(${interHotelOrder.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1] && interHotelOrder.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1].openDelete ? '-60px' : '0px'})`}">
                  <div class="logo" @click="_setPassengerOpenDelete(true, index, n)">
                    <div style="background-image: url('static/image/plane-ticket/book/delete.png')"></div>
                  </div>
                  <div class="details" @click="_gotoPassengerDetails(index, n)">
                    <p><template v-if="n === 1">{{$t('base.MainRoomer')}}</template></p>
                    <p v-if="interHotelOrder.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1]">
                      <template v-if="interHotelOrder.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1].DefaultNameType === 0">
                        {{interHotelOrder.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1].ChName}}
                      </template>
                      <template v-else>
                        {{interHotelOrder.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1].FirstName}}
                        {{interHotelOrder.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1].LastName}}
                      </template>
                    </p>
                    <p v-else>{{$t('base.PleaseAddARoomer')}}</p>
                  </div>
                  <div class="delete" @click="_deletePassenger(interHotelOrder.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1])">{{$t('base.Delete')}}</div>
                </div>
              </div>
            </div>
          </hs-cell-1>

          <hs-cell-1 :showLogo="false" v-if="details.ChargeInfo && (interHotelOrder.RoomAmount === index)">
            <!-- <div class="service-line"></div> -->
            <div class="service-fee">
              <p>{{$t('base.ServiceFee')}}</p>
              <p>￥{{details.ChargeInfo.ServiceCharge}}</p>
            </div>
          </hs-cell-1>
        </hs-section>

        <!-- 其他信息 -->
        <hs-section>
          <hs-cell-small-title @click.native="showArrivalTimeDialog = true"
            :title="$t('base.ArrivalHotelTime')"
            type="disabled-input"
            :value="interHotelOrder.ArrivalTime"
            :placeholder="`(${$t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`">
          </hs-cell-small-title>
          <hs-cell-small-title
            :title="$t('base.SpecificRequirements')"
            type="input"
            v-model="specificRequirements"
            :placeholder="`(${$t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.IsDisplayCustomItem" @click.native="showCCustomItem = true"
            :title="details.CustomItemName"
            type="disabled-input"
            :value="interHotelOrder.CustomItem ? interHotelOrder.CustomItem.Name: ''"
            :placeholder="`(${details.IsRequiredCustomItem ? $t('base.RequiredChoose') : $t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.IsDisplayPurpose"
            :title="$t('base.Purpose')"
            type="input"
            v-model="purpose"
            :placeholder="`(${details.IsRequiredPurpose ? $t('base.RequiredFill') : $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`"
            :showLogo="false">
          </hs-cell-small-title>
          <!-- TODO: 酒店授权码填写后台支持, 但是订单详情接口没有此字段, 所以暂时隐藏 -->
          <!-- <hs-cell-3 v-if="details.IsDisplayAuthorizationCode" title="授权码" :showLogo="false" titleColor="#333333">
            <input maxlength='50' style="width: 100%" v-model="authorizationCode" :placeholder="`(${details.IsRequiredAuthorizationCode ? '必填': '非必填'}) 请填写`"/>
          </hs-cell-3> -->
          <hs-cell-small-title v-if="details.VettingType === 3 && details.EnableVetting" @click.native="picker.show()"
            :title="$t('base.Approver')"
            type="disabled-input"
            :value="interHotelOrder.AppointVettingPersonName"
            :placeholder="`(${$t('base.RequiredChoose')}) ${$t('base.PleaseChoose')}`">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="Number(interHotelOrder.TravelType) === 0" @click.native="payTypePicker.show()"
            :title="$t('base.PaymentWay')"
            type="disabled-input"
            :value="interHotelOrder.PayType | filterPayType"
            :placeholder="`(${$t('base.RequiredChoose')}) ${$t('base.PleaseChoose')}`">
          </hs-cell-small-title>
        </hs-section>

        <!-- 联系人 -->
        <hs-section>
          <hs-cell-1 :value="$t('base.ContactInformation')" logoName="add.png" @click.native="$refs.CFrequentContacts.open()"></hs-cell-1>
          <hs-cell-1 v-for="(item, index) in interHotelOrder.Contacts" :key="index">
            <div class="people-infor" :style="{transform: `translateX(${!interHotelOrder.Contacts[index].openDelete ? '0px' : '-65px'})`}">
              <div class="left" @click="_setContactsOpenDelete(true, index)">
                <div class="logo" style="background-image: url('static/image/plane-ticket/book/delete.png')"></div>
              </div>
              <div class="right" @click="_gotoContactsDetails(index)">
                <div class="item">
                  <p class="name">{{item.Name}}</p>
                </div>
                <div class="item">
                  <p class="other-title">{{$t('base.CellphoneNumber')}}</p>
                  <p class="other-value">{{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                </div>
                <div class="item">
                  <p class="other-title">{{$t('base.Email')}}</p>
                  <p class="other-value">{{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
                <div class="delete" @click.stop="_deleteContact(index)">{{$t('base.Delete')}}</div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
      </ul>
    </hs-scroll>
    <div class="footer" @click="_switchDetailsDialog">
      <div class="price">
        <p v-if="details.ChargeInfo">￥{{details.ChargeInfo.TotalPrice}}</p>
        <div class="logo" :style="{transform: showDetailsDialog ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
      </div>
      <div class="details">
        <hs-gradient-button @click="_gotoConfirm"
          type="common"
          width="auto"
          :title="$t('base.NextStep')">
        </hs-gradient-button>
      </div>
    </div>
    <hs-dialog v-model="showDetailsDialog" height="calc(100% - 50px)" ref="detailsDialog">
      <hs-scroll class="detailsDialog" v-if="details.ChargeInfo" :data="[interHotelOrder]" :isListenScroll="true" @scrollStart="_scrollStart" @click.native.stop>
        <ul>
          <hs-cell-1 :showLogo="false">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.PriceHotel')}}</p>
              <p>
                <span>￥{{details.ChargeInfo.AverageRate * getApartDayCount(interHotelOrder.CheckInDate, interHotelOrder.CheckOutDate)}}</span>
                <span>x {{getApartDayCount(interHotelOrder.CheckInDate, interHotelOrder.CheckOutDate)}}{{$t('base.QuantifierNight')}}</span>
                <span>x {{interHotelOrder.RoomAmount}}{{$t('base.QuantifierHotel')}}</span>
              </p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.ServiceFee')}}</p>
              <p><span>￥{{details.ChargeInfo.ServiceCharge}}</span></p>
            </div>
          </hs-cell-1>
        </ul>
      </hs-scroll>
      <div class="show-more" ref="show-more">{{$t('base.SlideUpToSeeMore')}}</div>
    </hs-dialog>
    <hs-dialog v-model="showArrivalTimeDialog">
      <hs-scroll class="arrivalTimeDialog" :data="arrivalTimeItems" ref="arrivalTimeDialog">
        <ul>
          <div class="item-header">{{$t('base.ETA')}}</div>
          <div class="item-header-little">{{$t('base.TheRoomIsReservedAllNight')}}</div>
          <div class="items">
            <p
              v-for="(item, index) in arrivalTimeItems" :key="index"
              @click="_selectArrivalTimeItem(item)"
              :class="{actived: interHotelOrder.ArrivalTime === item}"
              :style="{'margin-right': index % 3 === 2 ? '0px' : '', 'margin-bottom': index % 3 === 0 ? '20px' : ''}">{{item}}</p>
          </div>
        </ul>
      </hs-scroll>
    </hs-dialog>

    <!-- 入住人列表 -->
    <template v-if="ratePlan && ratePlan.MaxAdultAmount">
      <c-passenger
        ref="CPassenger"
        :XOrder="interHotelOrder"
        :BookInit="details"
        :OrderBusinessType="11"
        :MaxLength="ratePlan.MaxAdultAmount"
        :GuestToRoomNo="GuestToRoomNo"
        @delegateWithConfirm="_delegateWithConfirm"
      ></c-passenger>
    </template>

    <!-- 入住人详情 -->
    <passenger-details
      ref="PassengerDetails"
      :details="editingPassenger"
      :BookInit="details"
      :OrderBusinessType="11"
      @delegate="_delegateWithPassengerDetails"
    ></passenger-details>

    <!-- 自定义项 -->
    <c-custom-item :show.sync="showCCustomItem" :orderInit.sync="details" @delegate="_delegateWithCustomItem"></c-custom-item>

    <!-- 联系人 -->
    <c-contacts :show.sync="showCContacts" v-model="contactDetails" :datalist="interHotelOrder.Contacts" :OrderBusinessType="2" @delegate="_delegateWithContacts"></c-contacts>

    <c-frequent-contacts ref="CFrequentContacts" :orderInit.sync="details" :datalist="interHotelOrder.Contacts" :OrderBusinessType="2" @delegate="_delegateWithFrequentContacts"></c-frequent-contacts>
    <violation-rank :show.sync="showViolationRank" :details="violationRankDetails" @delegate="_nextStep"></violation-rank>
  </div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsButton from 'base/hs-button/hs-button'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCell3 from 'base/hs-tableview/hs-cell-3/hs-cell-3'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsDialog from 'base/hs-dialog/hs-dialog'
import CPassenger from 'components/c-passenger/c-passenger'
import CCustomItem from 'components/c-passenger/c-custom-item'
import CContacts from 'components/c-passenger/c-contacts'
import CFrequentContacts from 'components/c-passenger/c-frequent-contacts'
import ViolationRank from './violation-rank'
import PassengerDetails from 'pages/personal/passengers/details.vue'
import {kpiInterHotelGetBookInitInfo, kpiInterHotelCheckVetting, kpiInterHotelCheckReasonCode, kpiIntlhotelReCalculateFee, kpiHotelCheckOrderRepeat} from 'api'
import {mapGetters, mapMutations} from 'vuex'
// import PassengerCell from './passenger-cell'
const PAY_TYPE_PICKER_DATA_1 = { text: Vue.prototype.$t('base.CompanyPayment'), value: 1 } // eslint-disable-line
const PAY_TYPE_PICKER_DATA_2 = { text: Vue.prototype.$t('base.PersonalPayment'), value: 2 } // eslint-disable-line
const PAY_TYPE_PICKER_DATA_3 = { text: `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`, value: 3 } // eslint-disable-line
export default {
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 限制只能由路由'/inter-hotel/inforDetails'进入这个页面
      if (from.path !== '/inter-hotel/inforDetails') {
        next(false)
      } else {
        next()
      }
    })
  },
  beforeRouteLeave (to, from, next) {
    if (this.$refs.CPassenger && this.$refs.CPassenger.show) {
      this.$refs.CPassenger.close()
      next(false)
    } else if (this.$refs.PassengerDetails && this.$refs.PassengerDetails.show) {
      this.$refs.PassengerDetails.close()
      next(false)
    } else {
      this.showViolationRank = false
      this.showCCustomItem = false
      this.showCContacts = false
      next()
    }
  },
  created() {
    if (this.noValue(this.interHotelOrder.TravelType)) {
      this.$router.replace('/home')
      return
    }
    // 重置酒店订单中一些内容
    this._resetContentInTheHotelOrder()

    // 初始化数据
    this._getDetails()

    // 初始化到达时间数据
    this._initArrivalTimeItems()
  },
  computed: {
    ...mapGetters([
      'interHotelOrder'
    ]),
    hotel() {
      // console.log('hotel: ', this.$route.params.hotel)
      return this.$route.params.hotel
    },
    room() {
      // console.log('room: ', this.$route.params.room)
      return this.$route.params.room
    },
    ratePlan() {
      console.log('ratePlan: ', this.$route.params.ratePlan)
      return this.$route.params.ratePlan
    },
    // 差旅目的
    purpose: {
      get: function() {
        return this.interHotelOrder.Purpose
      },
      set: function(val) {
        let res = JSON.parse(JSON.stringify(this.interHotelOrder))
        res.Purpose = val
        this.SET_INTER_HOTEL_ORDER(res)
      }
    },
    // 授权码
    authorizationCode: {
      get: function() {
        return this.interHotelOrder.AuthorizationCode
      },
      set: function(val) {
        let res = JSON.parse(JSON.stringify(this.interHotelOrder))
        res.AuthorizationCode = val
        this.SET_INTER_HOTEL_ORDER(res)
      }
    },
    specificRequirements: {
      get: function() {
        return this.interHotelOrder.SpecificRequirements
      },
      set: function(val) {
        let res = JSON.parse(JSON.stringify(this.interHotelOrder))
        res.SpecificRequirements = val
        this.SET_INTER_HOTEL_ORDER(res)
      }
    }
  },
  watch: {
    showArrivalTimeDialog(val) {
      if (val) {
        this._initArrivalTimeItems()
      }
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  data() {
    return {
      details: {},
      /** 价格明细 */
      showDetailsDialog: false,

      /** 房间数 */
      showRoomAmount: true,

      /** 房间号 */
      GuestToRoomNo: undefined,

      /** 到达时间 */
      showArrivalTimeDialog: false,
      arrivalTimeItems: [],

      /** 自定义项 */
      showCCustomItem: false,

      /** 联系人 */
      showCContacts: false,
      contactDetails: {},

      /** 违反差旅职级 */
      showViolationRank: false,
      violationRankDetails: {},

      /** 常旅客详情 */
      editingPassenger: {}
    }
  },
  methods: {
    ...mapMutations([
      'SET_INTER_HOTEL_ORDER'
    ]),
    _getDetails() {
      let params = {
        HotelID: this.interHotelOrder.HotelID,
        CheckInDate: this.beTimeNumber(this.interHotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.interHotelOrder.CheckOutDate),
        RoomID: this.interHotelOrder.RoomID,
        RatePlanID: this.interHotelOrder.RatePlanID,
        TravelType: this.interHotelOrder.TravelType,
        RoomAmount: this.interHotelOrder.RoomAmount
      }
      kpiInterHotelGetBookInitInfo(params).then((res) => {
        console.log('获取酒店订单填写初始信息: ', res)
        this.details = res.ResultData
        // 默认联系人
        if (!this.noValue(JSON.stringify(this.details.DefaultContacts))) {
          let resInter = JSON.parse(JSON.stringify(this.interHotelOrder))
          resInter.Contacts = [Object.assign(this.details.DefaultContact, {openDelete: false})]
          // resInter.Contacts = this.details.DefaultContacts.map(v => {
          //   v.openDelete = false
          //   return v
          // })
          this.SET_INTER_HOTEL_ORDER(resInter)
        }
        // =====================================================
        this._creatPayTypePicker(this.details.PayTypes)
        this._creatApproversPicker(this.details.Approvers)
        // =====================================================
        // FIXME: 后端初始化订单接口中的价格有误, 主动修改价格信息
        this._setRoomAmount(this.interHotelOrder.RoomAmount)
      })
    },
    _initArrivalTimeItems() {
      let today = this.beTime(new Date())
      let time = this.beTime(new Date(), 'hh:mm')
      let CheckInDate = this.interHotelOrder.CheckInDate
      let isToday = today === CheckInDate
      let res = [
        '14:00', '15:00', '16:00', '17:00', '18:00',
        '19:00', '20:00', '21:00', '22:00', '23:00',
        '23:59', `${this.$t('base.Tomorrow', 'function', 1)}`, `${this.$t('base.Tomorrow', 'function', 2)}`, `${this.$t('base.Tomorrow', 'function', 3)}`, `${this.$t('base.Tomorrow', 'function', 4)}`,
        `${this.$t('base.Tomorrow', 'function', 5)}`, `${this.$t('base.Tomorrow', 'function', 6)}`
      ]
      isToday && (res = res.filter(v => time < v))
      this.arrivalTimeItems = res
      if (res[0] > this.interHotelOrder.ArrivalTime) {
        this._selectArrivalTimeItem(res[0])
      }
    },

    /** 审批人picker */
    _creatApproversPicker(Approvers) {
      let data = []
      if (Approvers) {
        Approvers.forEach(element => {
          data.push({
            text: element.Name,
            value: element.ID
          })
        })
      }
      this.picker = this.$createPicker({
        title: this.$t('base.SelectApprover'),
        data: [data],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: this._selectApproversPickerHandle
      })
    },
    _selectApproversPickerHandle(val, index, text) {
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.AppointVettingPersonID = val[0]
      res.AppointVettingPersonName = text[0]
      this.SET_INTER_HOTEL_ORDER(res)
    },
    // =========================================

    /** 初始化支付方式picker */
    _creatPayTypePicker(PayTypes) {
      let data = []
      if (Number(this.interHotelOrder.TravelType) === 1) {
        data = [PAY_TYPE_PICKER_DATA_2]
      } else if (PayTypes) {
        PayTypes.forEach(element => {
          if (element === 1) {
            data.push(PAY_TYPE_PICKER_DATA_1)
          } else if (element === 2) {
            data.push(PAY_TYPE_PICKER_DATA_2)
          } else if (element === 3) {
            data.push(PAY_TYPE_PICKER_DATA_3)
          }
        })
      }
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.PayType = data[0] ? data[0].value : null
      this.SET_INTER_HOTEL_ORDER(res)
      this.payTypePicker = this.$createPicker({
        title: this.$t('base.PaymentWay'),
        data: [data],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: this._payTypePickerSelectHandle
      })
    },
    _payTypePickerSelectHandle(val, index, text) {
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.PayType = val[0]
      this.SET_INTER_HOTEL_ORDER(res)
    },
    // ===========================================================

    /** 费用明细 */
    _switchDetailsDialog() {
      if (this.showDetailsDialog) {
        this.$refs.detailsDialog.showDetails = false
      } else {
        console.log(this.$refs.detailsDialog)
        this.showDetailsDialog = true
        setTimeout(() => {
          let dom = this.$refs.detailsDialog.$children[0].scroll
          if (dom.wrapperHeight < dom.scrollerHeight) {
            this.$refs['show-more'].style.opacity = 1
          } else {
            this.$refs['show-more'].style.opacity = 0
          }
        }, 400)
      }
    },

    /** 设置房间数 */
    _setRoomAmount(n) {
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.RoomAmount = n
      this.SET_INTER_HOTEL_ORDER(res)
      let params = {
        HotelID: this.interHotelOrder.HotelID,
        CheckInDate: this.beTimeNumber(this.interHotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.interHotelOrder.CheckOutDate),
        RoomID: this.interHotelOrder.RoomID,
        RatePlanID: this.interHotelOrder.RatePlanID,
        RoomAmount: this.interHotelOrder.RoomAmount,
        TravelType: this.interHotelOrder.TravelType
      }
      kpiIntlhotelReCalculateFee(params).then((res) => {
        console.log('重新计算价格', res.ResultData)
        this.details.ChargeInfo = res.ResultData
        // 确认页面需要用
        let resInter = {...this.interHotelOrder}
        resInter.ChargeInfo = this.details.ChargeInfo
        this.SET_INTER_HOTEL_ORDER(resInter)
      })
    },

    /** 添加入住人 */
    _delegateWithAdd(index) {
      this.GuestToRoomNo = index
      this.$refs.CPassenger.open()
    },

    /** 常旅客列表确定回调 */
    _delegateWithConfirm(datalist) {
      // TODO: 进行到这里
      console.log('datalist: ', datalist)
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.Guests = datalist
      this.SET_INTER_HOTEL_ORDER(res)
    },

    /** 开关入住人 */
    _setPassengerOpenDelete(value, index, n) {
      if (this.interHotelOrder.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1]) {
        let res = JSON.parse(JSON.stringify(this.interHotelOrder))
        res.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1].openDelete = value
        this.SET_INTER_HOTEL_ORDER(res)
      }
    },

    /** 入住人详情 */
    _gotoPassengerDetails(index, n) {
      this.editingPassenger = this.interHotelOrder.Guests.filter(v => v.GuestToRoomNo === index - 1)[n - 1]
      if (this.editingPassenger) {
        if (this.editingPassenger.openDelete) {
          this._setPassengerOpenDelete(false, index, n)
        } else {
          this.$refs.PassengerDetails.open()
        }
      }
    },

    /** 修改入住人回调 */
    _delegateWithPassengerDetails(item) {
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      let index = this.interHotelOrder.Guests.map(v => v.ID).indexOf(item.ID)
      res.Guests.splice(index, 1, item)
      this.SET_INTER_HOTEL_ORDER(res)
    },

    /** 删除联系人 */
    _deletePassenger(item) {
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      let index = this.interHotelOrder.Guests.map(v => v.ID).indexOf(item.ID)
      res.Guests.splice(index, 1)
      this.SET_INTER_HOTEL_ORDER(res)
    },

    // 选择预计到达时间
    _selectArrivalTimeItem(item) {
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.ArrivalTime = item
      this.SET_INTER_HOTEL_ORDER(res)
    },

    // 选择自定义项
    _delegateWithCustomItem(item) {
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.CustomItem = item
      this.SET_INTER_HOTEL_ORDER(res)
    },

    // 保存联系人回调
    _delegateWithContacts(contacts) {
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.Contacts.splice(contacts.index, 1, Object.assign(contacts, {openDelete: false}))
      this.SET_INTER_HOTEL_ORDER(res)
    },
    _setContactsOpenDelete(value, index) {
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.Contacts[index].openDelete = value
      this.SET_INTER_HOTEL_ORDER(res)
    },
    _gotoContactsDetails(index) {
      if (this.interHotelOrder.Contacts[index].openDelete) {
        this._setContactsOpenDelete(false, index)
      } else {
        this.contactDetails = Object.assign(JSON.parse(JSON.stringify(this.interHotelOrder.Contacts[index])), {index: index})
        this.showCContacts = true
      }
    },
    _deleteContact(index) {
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.Contacts.splice(index, 1)
      this.SET_INTER_HOTEL_ORDER(res)
    },
    _delegateWithFrequentContacts(val) {
      console.log('选中的联系人: ', val)
      let res = JSON.parse(JSON.stringify(this.interHotelOrder))
      res.Contacts = val
      this.SET_INTER_HOTEL_ORDER(res)
    },
    // ===================================================================
    _scrollStart() {
      this.$refs['show-more'].style.opacity = 0
    },

    // 确定
    _gotoConfirm() {
      console.log(this.interHotelOrder)
      for (let index = 0; index < this.interHotelOrder.RoomAmount; index++) {
        let obj = this.interHotelOrder.Guests.filter(item => item.GuestToRoomNo === index)
        if (obj.length === 0) {
          this.$toast(this.$t('base.RoomNoXAtLeastChooseOneRoomer', 'function', index + 1))
          return
        }
      }
      if (this.noValue(JSON.stringify(this.details))) {
        this.$toast(this.$t('TM.TheSysteIsBusyPleaseReOperate'))
        this.$router.replace('/home')
        return
      }
      if (this.interHotelOrder.Guests.length === 0) {
        this.$toast(this.$t('base.PleaseAddARoomer'))
        return
      } else if (this.interHotelOrder.Guests.length < this.interHotelOrder.RoomAmount) {
        this.$toast(this.$t('TM.ChooseAtLeastXPeople', 'function', this.interHotelOrder.RoomAmount))
        return
      } else if (this.interHotelOrder.Guests.length > this.interHotelOrder.RoomAmount * this.room.Capcity) {
        this.$toast(this.$t('TM.ChooseUpToXPeople', 'function', this.interHotelOrder.RoomAmount * this.room.Capcity))
        return
      } else if (this.details.IsRequiredCustomItem && this.noValue(JSON.stringify(this.interHotelOrder.CustomItem))) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.details.CustomItemName))
        return
      } else if (this.interHotelOrder.Contacts.length === 0) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.Contacts')))
        return
      } else if (this.details.IsRequiredPurpose && this.noValue(this.interHotelOrder.Purpose)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.Purpose')))
        return
      } else if (this.interHotelOrder.Purpose.length > 30) {
        this.$toast(this.$t('TM.LengthMustNotExceedXDigits', 'function', {name: this.$t('base.Purpose'), number: 30}))
        return
      } else if (this.details.VettingType === 3 && this.details.EnableVetting && this.noValue(this.interHotelOrder.AppointVettingPersonName)) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.Approver')))
        return
      } else if (this.details.IsRequiredAuthorizationCode && this.noValue(this.interHotelOrder.AuthorizationCode)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.AuthorizationCode')))
        return
      } else if (this.interHotelOrder.AuthorizationCode.length > 20) {
        this.$toast(this.$t('TM.LengthMustNotExceedXDigits', 'function', {name: this.$t('base.AuthorizationCode'), number: 20}))
        return
      } else if (this.noValue(this.interHotelOrder.PayType)) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.PaymentWay')))
        return
      } else {
        for (let item of this.interHotelOrder.Guests) {
          if (this.noValue(item.Mobile)) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.CellphoneNumber')}))
            return
          } else if (this.details.IsRequiredCostCenter && this.noValue(JSON.stringify(item.CostCenter))) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.CostCenter')}))
            return
          } else if (this.details.IsRequiedDepartment && this.noValue(JSON.stringify(item.Department))) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.Department')}))
            return
          } else if (this.details.IsRequiredBusinessUnit && this.noValue(JSON.stringify(item.BusinessUnit))) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.details.BusinessUnitName}))
            return
          }
        }
        for (let item of this.interHotelOrder.Contacts) {
          if (this.noValue(item.Mobile)) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.CellphoneNumber')}))
            return
          } else if (this.noValue(item.Email) && (item.IsSendBookEmail || item.IsSendIssuedEmail || item.IsSendConfirmEmail)) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.Email')}))
            return
          }
        }
        /** 填写姓名 */
        let interHotelOrder = JSON.parse(JSON.stringify(this.interHotelOrder))
        interHotelOrder.Guests.forEach((element, index) => {
          interHotelOrder.Guests[index].Name = element.DefaultNameType === 0 ? element.ChName : `${element.LastName} ${element.FirstName}`
        })
        this.SET_INTER_HOTEL_ORDER(interHotelOrder)

        let maplist = new Map()
        if (this.interHotelOrder.Contacts.filter(a => !maplist.has(a.Mobile) && maplist.set(a.Mobile, 1)).length < this.interHotelOrder.Contacts.length) {
          this.$toast(this.$t('TM.ThereMustBeNoContactWithTheSamePhoneNumber'))
          return
        }
      }
      this._checkVetting()
    },
    // 校验审批
    _checkVetting() {
      let params = {
        HotelID: this.interHotelOrder.HotelID,
        CheckInDate: this.beTimeNumber(this.interHotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.interHotelOrder.CheckOutDate),
        RoomID: this.interHotelOrder.RoomID,
        RatePlanID: this.interHotelOrder.RatePlanID,
        CheckGuests: this.interHotelOrder.Guests,
        CustomItem: this.interHotelOrder.CustomItem,
        AppointVettingPersonID: this.interHotelOrder.AppointVettingPersonID,
        AppointVettingPersonName: this.interHotelOrder.AppointVettingPersonName,
        PayType: this.interHotelOrder.PayType,
        TravelType: Number(this.interHotelOrder.TravelType)
      }
      kpiInterHotelCheckVetting(params).then((res) => {
        console.log('校验审批: ', res)
        if (res.ResultData.IsNeedVetting === true && res.ResultData.IsSameVetting === false) {
          this.$alert({
            content: this.$t('TM.IfTheApproverIsInconsistent')
          })
        } else {
          if (res.ResultData.IsNeedVetting === true) {
            let interHotelOrder = JSON.parse(JSON.stringify(this.interHotelOrder))
            interHotelOrder.VettingProcessList = res.ResultData.VettingProcessList
            this.SET_INTER_HOTEL_ORDER(interHotelOrder)
          }
          this._checkReason()
        }
      })
    },
    // 检查原因
    _checkReason() {
      let params = JSON.parse(JSON.stringify(this.interHotelOrder))
      params.CheckGuests = this.interHotelOrder.Guests
      params.CheckInDate = this.beTimeNumber(this.interHotelOrder.CheckInDate)
      params.CheckOutDate = this.beTimeNumber(this.interHotelOrder.CheckOutDate)
      kpiInterHotelCheckReasonCode(params).then((res) => {
        console.log('获取酒店reasoncode: ', res)
        if (res.ResultData.IsViolate === true) {
          let names = ''
          if (res.ResultData.GuestNames) {
            res.ResultData.GuestNames.forEach(element => {
              if (element.ViolateRankResult === 2) {
                names += `${element.Name}、`
              }
            })
          }
          if (!this.noValue(names)) {
            names = names.substring(0, names.length - 1)
            this.$toast(this.$t('TM.XIsNotAllowedToBook', 'function', names))
          } else {
            this.showViolationRank = true
            this.violationRankDetails = res.ResultData
          }
        } else {
          this._checkOrderRepeat()
        }
      })
    },
    /**
     * 检验酒店重复下单
     */
    _checkOrderRepeat() {
      let params = {
        CheckInDate: this.beTimeNumber(this.interHotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.interHotelOrder.CheckOutDate),
        Guests: this.interHotelOrder.Guests
      }
      kpiHotelCheckOrderRepeat(params).then((res) => {
        console.log('检验酒店重复下单: ', res)
        if (res.ResultData.IsRepeat) {
          this.$alert({
            content: res.ResultData.Message,
            cancelTxt: this.$t('base.Cancel'),
            confirmTxt: this.$t('base.Continue'),
            confirmFn: () => {
              this._nextStep()
            }
          })
        } else {
          this._nextStep()
        }
      })
    },
    // 下一步
    _nextStep() {
      this.$router.push({name: 'interHotelConfirm', params: Object.assign(this.$route.params, {hotelOrderInit: this.details, room: this.room, ratePlan: this.ratePlan})})
    },

    /**
     * 重置酒店订单中一些内容
     */
    _resetContentInTheHotelOrder() {
      let res = {...this.interHotelOrder}
      res.ViolationRankName = ''
      res.ViolationRankReason = ''
      res.Guests = []
      this.SET_INTER_HOTEL_ORDER(res)
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsButton,
    HsGradientButton,
    HsSection,
    HsCell1,
    HsCell3,
    HsCellSmallTitle,
    HsDialog,
    CPassenger,
    CCustomItem,
    CContacts,
    CFrequentContacts,
    ViolationRank,
    PassengerDetails
    // PassengerCell
  }
}
</script>
